<template>
	<div id="message">
    <div class="header">
      <div class="van-hairline--bottom color_ffffff van-nav-bar">
        <div class="van-ellipsis van-nav-bar__title" style="color: #000 !important;">系统消息</div>
      </div>
    </div>
    <van-tabs v-model="active">
      <!--<van-tab title="系统消息">-->
        <div>
          <!--<div style="width: 100%;height: 40px;line-height: 30px;background-color: #ffffff;padding-top: 10px" class="text_center">-->
            <!--<span style="display: inline-block;width: 48%;background-color: #40a0f3;border-radius: 5px;color: #ffffff">系统</span>-->
            <!--<span style="display: inline-block;width: 48%;background-color: #e5e5e5;border-radius: 5px;color: #8b8b8b">互动消息</span>-->
          <!--</div>-->
          <div style="margin-top: 5px;padding: 5px;border-bottom: 5px solid #f4f4f4;" v-for="(item,index) in messageList">
            <div class="text_center font_12 color_8b8b8b" style="margin-top: 5px;">
              <span>{{item.addtime}}</span>
            </div>
            <div class="background_color_ffffff font_12" style="margin: 0 10px">
              <!--<div style="border-bottom: 1px solid #eeeeee;padding: 6px">-->
                <!--<span style="margin-top: 2px;">{{item.addtime}}</span>-->
              <!--</div>-->
              <div style="padding: 6px">
                <span class="color_8b8b8b">{{item.content}}</span>
              </div>
              <!--<div style="padding: 6px;text-align: right">-->
                <!--<span class="color_8b8b8b" @click="toMessageInfo(item)">查看详情</span>-->
              <!--</div>-->
            </div>
          </div>
        </div>
      </van-tab>
      <!--<van-tab title="互动消息">-->
        <!--<div class="chat-bar background_color_ffffff" v-for="(item,index) in chatList" :key="index" @click="toChat(item)">-->
          <!--<img :src="item.shoplogo" width="14%" style="vertical-align: middle;"/>-->
          <!--<span>{{item.shopname}}</span>-->
          <!--<span class="float_right font_12 color_8b8b8b">{{item.chat.addtime}}</span>-->
        <!--</div>-->
      <!--</van-tab>-->
    </van-tabs>
	</div>
</template>
<script>
	export default {
		name: 'message',
		data() {
			return {
			  active:0,
        userId:localStorage.getItem('userId'),
        messageList:[],
        chatList:[],
			}
		},
    mounted() {
      this.getMessageList();
      // this.getChatList();
    },
		methods:{
      toChat(dataMap){
        let chat = {
          shopid:dataMap.shopid,
          shoplogo:dataMap.shoplogo,
          shopname:dataMap.shopname,
        };
        this.$router.push({path:'chat',query:{data:chat}});
      },
      toMessageInfo(dataMap){
        this.$router.push({path:'messageInfo',query:{data:dataMap}});
      },
      getChatList(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("chat/chatlistByuserid", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.chatList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      getMessageList(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("news/getnewslist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.messageList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style>
  #message .chat-bar{
    margin-top: 5px;
    padding: 10px;
    display: flex;
    align-items: center;
    width: 95%;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  #message .van-tabs__nav--line {
    height: 43px !important;
    position: fixed !important;
    width: 100% !important;
    padding-bottom: 0px !important;
  }
  #message .van-tab--active {
    color: #c10804 !important;
  }
  #message .van-tabs__line {
    background-color: #c10804;
  }
  #message{
    margin-bottom: 56px;
    background-color: #ffffff;
  }
  #message .van-tabs__wrap {
    z-index: 99999;
  }
</style>



